import { ButtonHTMLAttributes, ReactNode } from 'react'
import { Slot } from '@radix-ui/react-slot'
import { clsx } from 'clsx'

export interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  children: ReactNode
  asChild?: boolean
}

export function Button({ children, asChild, className, ...props }: ButtonProps): JSX.Element {
  const Comp = asChild ? Slot : 'button'

  return (
    <Comp
      {...props}
      className={clsx('py-4 px-3 bg-cyan-500 rounded font-semibold text-black text-sm w-full hover:bg-cyan-300 transition-colors focus:ring-2 ring-white outline-none', {

      }, className)}
    >{children}</Comp>
  )
}